    padding: 20px;
}

.textarea {
    width: 400px;
    height: 200px;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.word-count {
    margin-top: 10px;
    font-size: 18px;
}